<template>
    <layout title="设置">
        <div class="login-container" ref="node">
            <div class="header">
                <img :src="photo" alt="">
                <i @click="pickImg"></i>
            </div>
            <span class="name">{{userInfo.name}}</span>
            <ul>
                <li @click="clearCache">清除缓存</li>
                <li @click="$router.push('/version')" class="msg">版本信息</li>
                <li @click="$router.push('/operate')" class="msg">操作指南</li>
            </ul>
            <van-button @click="quitSystem" block type="primary">退出系统</van-button>
        </div>
    </layout>
</template>

<script>
    import UserImg from '../../images/default-icon.png'
    import { base64UploadApi } from '../../apis/SettingApi'
    import { userSaveApi } from '../../apis/UserApi'

    export default {
        data() {
            return {
                photo: ''
            }
        },
        computed: {
            userInfo() {
                return this.$store.getters['common/getUserInfo']
            }
        },
        created() {
            if (!!this.userInfo.photo) {
                this.photo = window.BASE_URL + this.userInfo.photo
            } else {
                this.photo = UserImg
            }
        },
        methods: {
            pickImg() {
                let ipt = document.createElement('input')
                ipt.setAttribute('type', 'file')
                ipt.onchange = () => {
                    this.$toast.loading({
                        message: '文件上传中...',
                        forbidClick: true,
                        duration: 0
                    })

                    let fr = new FileReader()
                    fr.onload = () => {
                        let canvas = document.createElement('canvas'),
                            ctx = canvas.getContext('2d'),
                            img = new Image()

                        img.onload = async () => {
                            canvas.width = 256
                            canvas.height = 256

                            ctx.drawImage(img, 0, 0, img.width, img.height, 0, 0, 256, 256)
                            let source = canvas.toDataURL()
                            console.log(this.photo);
                            console.log(this.photo.length);

                            let result = await base64UploadApi({
                                type: 'photo',
                                data: source,
                                name: ipt.files[0].name
                            })
                            this.$toast.clear()
                            if (result.code !== 200) {
                                this.$toast.fail('文件上传失败，请重试')
                                return
                            }

                            let res = await userSaveApi({id: this.userInfo.id, photo: result.data})
                            if (res.code === 200) {
                                this.$store.commit('common/mutationUserInfo', {...this.userInfo, photo: result.data})
                                this.photo = window.BASE_URL + this.userInfo.photo
                            }
                        }
                        img.src = fr.result
                    }
                    fr.readAsDataURL(ipt.files[0])
                }
                ipt.click()
            },
            clearCache() {
                this.$dialog.confirm({
                    title: '清除确认',
                    message: '清除信息后，需要重新登录，确定要清除？'
                }).then(() => {
                    sessionStorage.clear()
                    localStorage.clear()
                    this.$router.push('/login')
                }).catch(() => {})
            },
            quitSystem() {
                window.plus.runtime.quit()
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../../style/common.less";

    .login-container {
        display: block;
        padding: 20px 16px;
        .header {
            display: block;
            position: relative;
            width: 140px;
            height: 140px;
            border: 2px solid @primary;
            border-radius: 50%;
            margin: 0 auto 20px;
            img {
                display: block;
                width: 124px;
                height: 124px;
                margin: 6px;
                border-radius: 50%;
            }
            i {
                display: block;
                position: absolute;
                top: -10px;
                left: calc(50% + 60px);
                width: 46px;
                height: 46px;
                background: url(../../images/edit-icon.png) center center / 36px 36px no-repeat;
            }
        }
        .name {
            display: block;
            position: relative;
            margin-top: -10px;
            margin-bottom: 12px;
            font-size: .8rem;
            color: @primary;
            text-align: center;
        }
    }
    ul {
        display: block;
        margin-bottom: 20px;
        li {
            position: relative;
            border-bottom: solid 1px @border;
            line-height: 46px;
            font-size: .6rem;
            color: @999;
            padding-left: 20px;
            &::after {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                right: 20px;
                width: 36px;
                height: 46px;
                background: url(../../images/setting-gray.png) center center / 24px 24px no-repeat;
            }
            &.msg::after {
                background-image: url(../../images/search-gray.png);
            }
        }
    }
</style>